import http from "./api";
var tableName  = '';
var Data = {};
var tableID = '';

//+
function add(tableName,Data){
    if(tableName=='' || Data == null ||JSON.stringify(Data) == ''){
        return '';
    }

    http.post(`${tableName}`,Data).then(res=>{
        console.log(res)
        return res;
    }).catch(err=>{
        return err;
    })
}

//
function updated(tableID,tableName,Data){
    if(tableName=='' || Data == null || Data == ''){
        return '';
    }
    if(tableID == ''){
        return 'ID';
    }
    http.put(`${tableName}/${tableID}`,Data).then(res=>{
        return res;
    }).catch(err=>{
        return err;
    })
}
//
function dele(tableID,tableName){
    if(tableName==''){
        return '';
    }
    if(tableID == ''){
        return 'ID';
    }
    http.delete(`${tableName}/${tableID}`).then(res=>{
        return res;
    }).catch(err=>{
        let error =  errorlist.filter(f=>f === err.status);
        console.log(error);
        return err;
    })
}
//
function select(tableName){
    if(tableName == ''){
        return ''
    }
    http.get(`${tableName}`).then(res=>{
        return res;
    }).catch(err=>{
        let error =  errorlist.filter(f=>f === err.status);
        console.log(error);
        return err;
    })
}
